.simplepicker-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
  background-color: #4c4c4c7d;
  width: 100vw;
  height: 100vh;
  display: none;
}

.simplepicker-wrapper.active {
  display: block;
}

.simpilepicker-date-picker {
  width: 326px;
  cursor: initial;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 14px;
  background-color:  rgb(145, 41, 41);     /*edf #fff;  */
  margin: 2% auto;
  box-shadow: 1px 2px 4px 0.5px rgba(0, 0, 0, 0.22),
              1px 2px 4px 0.5px rgba(0, 0, 0, 0.);
}

.simplepicker-day-header {
  display: none;      /*edf*/
  color: #fff;
  background-color: #016565;
  text-align: center;
  padding: 4px 0;
}

.simplepicker-date-section {
  display: none;                /*edf added*/
  text-align: center;
  background-color: #078e8e;
  color: #fff;
}

.simplepicker-date-section .simplepicker-month-and-year {
  font-size: 22px;
  padding: 14px 0 7px 0;
  font-weight: 200;
}

.simplepicker-date-section .simplepicker-date {
  font-size: 35px;
  padding: 2px;
  margin-bottom: 12px;
}

.simplepicker-select-pane {
  display: flex;
  width: 100%;
}

.simplepicker-select-pane .simplepicker-time,
.simplepicker-selected-date {
  text-align: center;
  flex: 2;
}

.simplepicker-icon, .simplepicker-time,
.simplepicker-selected-date {
  border: none;
  background: transparent;
  padding: 2px;
  margin: 4px 6px;
  width: 25px;
  height: 25px;
  opacity: 0.5;
}

.simplepicker-time, .simplepicker-selected-date {
  opacity: 1;
}

.simplepicker-month-switcher {
  font-size: 18px;      /*edf*/
  margin-top: 5px;
}

.simplepicker-icon:hover {
  opacity: 0.8;
}

.simplepicker-icon:active, .simplepicker-icon.active {
  opacity: 1;
}

.simplepicker-icon:focus {
  outline: none;
}

.simplepicker-icon-next {
  background-image: url(data:image/svg+xml;base64,);
}

.simplepicker-icon-previous {
  background-image: url(data:image/svg+xml;base64,);
}

.simplepicker-icon-calender {
  background-image: url(data:image/svg+xml;base64,);
}

.simplepicker-icon-time {
  background-image: url(data:image/svg+xml;base64,);
}

.simplepicker-bottom-part {
  padding-top: 6px;
  padding-bottom: 12px;
  padding-left: 4%;         /*edf*/
}

.simplepicker-calender table {
  width: 100%;
  font-size: 13px;
}

.simplepicker-calender thead th {
  font-weight: normal;
  font-size: 15px;
  padding-bottom: 6px;
  width: 14.28%;
  height: 30px;
  text-align: center;
}

.simplepicker-calender tbody td {
  position: relative;
  text-align: center;
  vertical-align: middle;
  padding: 12px;
  z-index: 1;
}

.simplepicker-calender tbody td::after {
  content: " ";
  visibility: hidden;
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  height: 35px;
  width: 35px;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.15);  /*edf 0.05 -> 0.15  */
}

.simplepicker-calender tbody td:hover::after {
  visibility: visible;
}

/*edf separated the 2 following entries*/
.simplepicker-calender tbody td.active {
  pointer-events: none;                 /*edf added*/
  color: #fff;
}

.simplepicker-calender tbody td:active {
  color: #fff;
}

.simplepicker-calender tbody td:active::after,
.simplepicker-calender tbody .active::after {
  visibility: visible;
  background-color:teal;
  z-index: -1;
}

.simplepicker-calender tbody td[data-empty] {
  pointer-events: none;
}

.simplepicker-time-section {
  display: block;         /*edf none; */
  height: 45px;           /*edf 120px; */
  text-align: center;     /*edf*/
}

.simplepicker-time-section #invisbl {
  visibility: hidden;
}

.simplepicker-time-section input {
  background-color: teal;   /*edf*/
  box-sizing: border-box;  /* border-box */
  width: 135px;             /*135 edf*/
  height: 32px;
  max-width: 155px;         /*155 edf*/
  max-height: 34px;         /*edf*/
  padding: 6px 5px;        /*edf*/
  border: none;
  /* border-bottom: 2px solid black;     edf*/
  font-size: 16px;          /*edf*/
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: #fff;              /*edf*/
  display: inline-block;
  margin: auto;          /*edf*/
}

.simplepicker-time-section input:focus {
  outline: none;
  border-bottom-color: black;   /*edf*/
}

.simplepicker-picker-section {
  width: 100%;
  height: auto;
}

.simplepicker-btn {
  border: none;
  background: transparent;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
  padding-top: 8px;
  padding-bottom: 8px;
  border: 1px solid #ddd;
  margin: 0 8px;                  /*edf*/
  border-color: #000000;          /*edf #016565; */
  color: #000000;                 /*edf #016565; */
  font-size: 13px;
  font-weight: bold               /*edf*/
}

.simplepicker-bottom-part {
  height: 42px;
  padding: 8px;
}

.simplepicker-cancel-btn.simplepicker-btn {
float: left;
}
.simplepicker-ok-btn.simplepicker-btn {
float: right;
}


.simplepicker-btn:hover, .simplepicker-btn:active {
  color: white;
  background-color: #016565;
}

.simplepicker-fade {
  color: rgba(225, 225, 225, 0.95);
}

.simplepicker-wrapper, .simplepicker-icon,
.simplepicker-btn {
  cursor: pointer;
}
